#block-main-prehome-cup {
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 10;
  width: 100%;
  height: 1000px; }
  #block-main-prehome-cup .fullscreen {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%; }
  #block-main-prehome-cup .block__content {
    background: url("../../../images/prehome-cup/background.png");
    background-position: 50% 50%;
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%; }
    #block-main-prehome-cup .block__content .background-overlay {
      background: radial-gradient(transparent, black);
      background-position: 50% 50%; }
  #block-main-prehome-cup .prehome-cup {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%) !important;
    -webkit-transform: translateX(-50%) translateY(-50%) !important;
    -moz-transform: translateX(-50%) translateY(-50%) !important;
    -o-transform: translateX(-50%) translateY(-50%) !important;
    -ms-transform: translateX(-50%) translateY(-50%) !important;
    text-align: center; }
  #block-main-prehome-cup #patterns {
    width: 495px;
    height: 132px; }
  #block-main-prehome-cup canvas.smoke {
    position: absolute;
    top: -230px;
    left: 50%;
    transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    -ms-transform: translateX(-50%); }
  #block-main-prehome-cup .cup {
    width: 339px;
    height: 138px;
    background: url("../../../images/prehome-cup/cup.png");
    margin: auto;
    position: relative;
    left: 15px; }
  #block-main-prehome-cup .texts {
    color: #edc41b;
    font-family: Dancing;
    font-size: 35px; }
    #block-main-prehome-cup .texts ul li {
      color: #edc41b;
      background-image: -webkit-gradient(linear, left top, right top, color-stop(0.43, #f6bf08), color-stop(0.68, white), color-stop(0.96, #f6bf08));
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      line-height: 40px; }
